import Api from "./Api"
import Cookie from 'js-cookie'

export default {
  getCookie() {
    let token = Cookie.get('XSRF-TOKEN')

    if (token) {
      return new Promise(resolve => {
        // console.log(token)
        resolve(token)
      })
    }

    // return Api.get("csrf-cookie").then((response) => {
    //   console.log(response)
    // })
    return Api.get("csrf-cookie");
  }
}
