<script>
import _ from 'lodash'
import ModalUser from '@/components/app/ModalUser.vue'

export default {
  name: 'Dashboard',
  component: {
    ModalUser
  },
  data() {
    return {
      routes: this.$router.options.routes[0].children, // Access to DASH
      expandOnHover: false,
      arrow: 'arrow-right-bold',
      mobile: 'hide',
      reduce: false,
      open: true,
      spaced: true,
      shadow: true,
      fixed: true,
      isOpened: false
      // criptosil: {}
    }
  },
  methods: {
    isActive: function (item) {
      return _.find(item.children,{ name: this.$route.name }) !== undefined
    },
    UserModal: function () {
      this.$buefy.modal.open({
        parent: this,
        hasModalCard: true,
        trapFocus: true,
        component: ModalUser
      })
    },

    isMobile: function () {
      if(this.mobile === 'hide'){
        this.mobile = 'fullwidth'
        this.arrow = 'arrow-left-bold'
      } else if(this.mobile === 'fullwidth'){
        this.mobile = 'hide'
        this.arrow = 'arrow-right-bold'
      }
    },

    toggleActive: function (){
      this.isOpened = !this.isOpened
    }
  },
  mounted () {
    // console.log(this.$store.state.authenticated)
    // this.criptosil = this.$store.state.user
  }
}
</script>
